import { useState, useContext } from 'react';
import { MovieContext } from '../../contexts/MovieContext'; 
import { setMoviesToLocalStorage } from '../../utils/localStorageUtils';
import '../../styles/VideoManagement.css';

export const VideoManagement = () => {
  
  const { movies, setMovies } = useContext(MovieContext);
  const [newVideo, setNewVideo] = useState({
    name: '', // 영화 이름은 'name'으로 통일하는 것이 좋습니다.
    genre: '', // 영화의 장르를 나타내는 'genre'
    src: '', // 이미지 경로
    info: {
        broadcasting: '',
        productionYear: '',
        casting: [],
        rating: '',
        score: '',
        summary: '',
    },
  });  

  // 수정 모드 상태
  const [editMode, setEditMode] = useState(false);
  const [editVideoId, setEditVideoId] = useState(null);
  
  // 배우 입력을 위한 임시 상태
  const [castingInput, setCastingInput] = useState('');

  // 영상 추가 핸들러
  const handleAddVideo = (e) => {
    e.preventDefault();
    
    // 배우 이름을 배열로 변환 (쉼표로 구분)
    const castingArray = castingInput.split(',').map(actor => actor.trim()).filter(actor => actor);
    
    // 새 영화 객체 생성
    const videoToAdd = {
      ...newVideo,
      info: {
        ...newVideo.info,
        casting: castingArray
      }
    };
    
    // 수정 모드인 경우
    if (editMode && editVideoId) {
      const updatedMovies = movies.map(movie => 
        movie.id === editVideoId ? { ...videoToAdd, id: editVideoId } : movie
      );
      setMovies(updatedMovies);
      setMoviesToLocalStorage(updatedMovies);
      setEditMode(false);
      setEditVideoId(null);
    } 
    // 추가 모드인 경우
    else {
      const newId = (movies.length > 0 ? Math.max(...movies.map(m => parseInt(m.id))) + 1 : 1).toString();
      const updatedMovies = [...movies, { ...videoToAdd, id: newId }];
      setMovies(updatedMovies);
      setMoviesToLocalStorage(updatedMovies);
    }
    
    // 폼 초기화
    resetForm();
  };

  // 영상 삭제 핸들러
  const handleDeleteVideo = (id) => {
    const updatedMovies = movies.filter(movie => movie.id !== id);
    setMovies(updatedMovies);
    setMoviesToLocalStorage(updatedMovies);
    
    // 수정 중이던 영상이 삭제된 경우 폼 초기화
    if (editVideoId === id) {
      resetForm();
    }
  };
  // 수정 버튼 클릭 핸들러
  const handleEditClick = (movie) => {
    setEditMode(true);
    setEditVideoId(movie.id);
    
    // 폼에 영상 정보 채우기
    setNewVideo({
      name: movie.name,
      genre: movie.genre,
      src: movie.src,
      info: {
        broadcasting: movie.info.broadcasting || '',
        productionYear: movie.info.productionYear || '',
        rating: movie.info.rating || '',
        score: movie.info.score || '',
        summary: movie.info.summary || ''
      }
    });
    
    // 배우 배열을 문자열로 변환하여 설정
    setCastingInput(movie.info.casting ? movie.info.casting.join(', ') : '');
  };
  // 폼 초기화 함수
  const resetForm = () => {
    setNewVideo({
      name: '',
      genre: '',
      src: '',
      info: {
        broadcasting: '',
        productionYear: '',
        casting: [],
        rating: '',
        score: '',
        summary: ''
      }
    });
    setCastingInput('');
    setEditMode(false);
    setEditVideoId(null);
  };

  return (
    <div className="video-management">
      <h2>영상 관리</h2>
      
      <div className="add-video-form">
        <h3>{editMode ? '영상 수정' : '새 영상 추가'}</h3>
        <form onSubmit={handleAddVideo}>
          <div className="form-row">
            <div className="form-group">
              <label>제목</label>
              <input 
                type="text" 
                value={newVideo.name} 
                onChange={(e) => setNewVideo({...newVideo, name: e.target.value})}
                required
              />
            </div>
            
            <div className="form-group">
              <label>장르 (카테고리)</label>
              <select 
                value={newVideo.genre}
                onChange={(e) => setNewVideo({...newVideo, genre: e.target.value})}
                required
              >
                <option value="">선택하세요</option>
                <option value="한국영화">한국영화</option>
                <option value="한국드라마">한국드라마</option>
                <option value="해외영화">해외영화</option>
                <option value="해외드라마">해외드라마</option>
                <option value="예능">예능</option>
                <option value="스포츠">스포츠</option>
                <option value="다큐멘터리">다큐멘터리</option>
                <option value="키즈">키즈</option>
                <option value="인기작">인기작</option>
                <option value="신작">신작</option>
                <option value="추천 영상">추천 영상</option>
              </select>
            </div>
          </div>
          <div className="form-group">
            <label>이미지 경로</label>
            <input 
              type="text" 
              value={newVideo.src} 
              onChange={(e) => setNewVideo({...newVideo, src: e.target.value})}
              placeholder="/img/이미지파일명.jpg"
              required
            />
          </div>
          
          <h4>상세 정보</h4>
          
          <div className="form-row">
            <div className="form-group">
              <label>편성</label>
              <input 
                type="text" 
                value={newVideo.info.broadcasting} 
                onChange={(e) => setNewVideo({
                  ...newVideo, 
                  info: {...newVideo.info, broadcasting: e.target.value}
                })}
              />
            </div>
            
            <div className="form-group">
              <label>제작년도</label>
              <input 
                type="text" 
                value={newVideo.info.productionYear} 
                onChange={(e) => setNewVideo({
                  ...newVideo, 
                  info: {...newVideo.info, productionYear: e.target.value}
                })}
              />
            </div>
          </div>
          
          <div className="form-group">
            <label>출연진 (쉼표로 구분)</label>
            <input 
              type="text" 
              value={castingInput} 
              onChange={(e) => setCastingInput(e.target.value)}
              placeholder="배우1, 배우2, 배우3"
            />
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label>등급</label>
              <select 
                value={newVideo.info.rating} 
                onChange={(e) => setNewVideo({
                  ...newVideo, 
                  info: {...newVideo.info, rating: e.target.value}
                })}
              >
                <option value="">선택하세요</option>
                <option value="전체">전체 관람가</option>
                <option value="12+">12세 이상</option>
                <option value="15+">15세 이상</option>
                <option value="19+">19세 이상</option>
              </select>
            </div>
            
            <div className="form-group">
              <label>평점</label>
              <input 
                type="text" 
                value={newVideo.info.score} 
                onChange={(e) => setNewVideo({
                  ...newVideo, 
                  info: {...newVideo.info, score: e.target.value}
                })}
                placeholder="예: 8.5"
              />
            </div>
          </div>
          
          <div className="form-group">
            <label>줄거리</label>
            <textarea 
              value={newVideo.info.summary} 
              onChange={(e) => setNewVideo({
                ...newVideo, 
                info: {...newVideo.info, summary: e.target.value}
              })}
              rows="4"
              placeholder="영상의 줄거리를 입력하세요"
            />
          </div>
          
          <div className="form-buttons">
            <button type="submit">{editMode ? '수정 완료' : '영상 추가'}</button>
            {editMode && (
              <button type="button" onClick={resetForm}>수정 취소</button>
            )}
          </div>
        </form>
      </div>
      
      <div className="video-list">
        <h3>영상 목록</h3>
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>제목</th>
              <th>장르</th>
              <th>관리</th>
            </tr>
          </thead>
          <tbody>
            {movies.map(movie => (
              <tr key={movie.id}>
                <td>{movie.id}</td>
                <td>{movie.name}</td>
                <td>{movie.genre}</td>
                <td>
                  <button onClick={() => handleEditClick(movie)}>수정</button>
                  <button onClick={() => handleDeleteVideo(movie.id)}>삭제</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};