// src/contexts/UserContext.js
import React, { createContext, useState, useEffect } from 'react';

// UserContext의 초기값 정의: user 객체와 인증 관련 함수들을 포함
export const UserContext = createContext({
  user: null,         // 현재 로그인된 사용자 정보
  signup: () => false, // 회원가입 함수 (성공 여부 반환)
  login: () => false,  // 로그인 함수 (성공 여부 반환)
  logout: () => {},    // 로그아웃 함수
  // 필요하다면 다른 사용자 관련 상태나 함수 추가 가능
});

// Admin 계정 시드 함수 (로컬스토리지에 users 키로 저장)
const seedAdmin = () => {
  // 회원가입에서 사용한 'registeredUsers' 키를 사용.
  const users = JSON.parse(localStorage.getItem('registeredUsers') || '[]');
  const exists = users.find(u => u.userId === 'admin'); // userId를 사용
  if (!exists) {
    users.push({ 
      userId: 'admin', 
      password: 'admin123', 
      role: 'admin', 
      name: 'Site Admin', 
      phone: '', 
      birth: '', 
      address: '', 
      email: '' ,
      userData: { '시청중': [], '찜한 영상': [], '본 영상': [] }
    }); // 나머지 필드 추가
    localStorage.setItem('registeredUsers', JSON.stringify(users));
  }
};

export const UserProvider = ({ children }) => {
  // localStorage에서 'currentUser' 정보를 읽어와 user 상태 초기화
  const [user, setUser] = useState(() => JSON.parse(localStorage.getItem('currentUser') || 'null'));

  // 컴포넌트 마운트 시 seedAdmin 실행하여 관리자 계정 생성
  useEffect(() => {
    seedAdmin();
    // 이 useEffect는 초기 마운트 시 `user` 상태를 로컬 스토리지에서 한번 더 확인하는 역할만 함.
    // `useState`의 초기값으로 대부분 처리되지만, 만약을 위해 남겨두는 것도 좋음.
    const storedUser = JSON.parse(localStorage.getItem('currentUser') || 'null');
    if (storedUser) {
        setUser(storedUser);
    }
  }, []);

  // 회원가입 함수
  const signup = ({ userId, password, name, phone, birth, address, email, userData }) => {
    const users = JSON.parse(localStorage.getItem('registeredUsers') || '[]');
    if (users.find(u => u.userId === userId)) {
      alert('이미 존재하는 아이디입니다.');
      return false;
    }

    const newUser = { userId, password, name, role: 'user', phone, birth, address, email, 
      userData: { '시청중': [], '찜한 영상': [], '본 영상': [] } 
    };
    users.push(newUser);
    localStorage.setItem('registeredUsers', JSON.stringify(users));
    // 회원가입 후 자동 로그인하지 않는다면 아래 setUser, localStorage.setItem('currentUser')는 제거
    // localStorage.setItem('currentUser', JSON.stringify({ userId: newUser.userId, name: newUser.name, role: newUser.role }));
    // setUser({ userId: newUser.userId, name: newUser.name, role: newUser.role });
    return true;
  };

  // 로그인 함수
  const login = ({ userId, password }) => {
    // 항상 관리자 계정이 존재하는지 확인
    // 혹시 모를 상황 대비하여 로그인 시에도 Admin 시드 실행
    seedAdmin(); 

    const users = JSON.parse(localStorage.getItem('registeredUsers') || '[]');
    const found = users.find(u => u.userId === userId && u.password === password);
    if (!found) {
      alert('아이디 또는 비밀번호가 올바르지 않습니다.');
      return null;    
    }
    // found.userData가 없으면 기본값 생성
    if (!found.userData) {
      found.userData = { '시청중': [], '찜한 영상': [], '본 영상': [] };
      // registeredUsers에도 바로 업데이트
      const userIndex = users.findIndex(u => u.userId === userId);
      if (userIndex !== -1) {
        users[userIndex].userData = found.userData;
        localStorage.setItem('registeredUsers', JSON.stringify(users));
      }
    }    
    const safeUser = { id: found.userId, name: found.userId, role: found.role || 'user', films: found.userData }; // Context에 저장할 때는 id를 'userId' 대신 'id'로 통일
    localStorage.setItem('currentUser', JSON.stringify(safeUser));
    setUser(safeUser);
    return safeUser;
  };

  // 로그아웃 함수
  const logout = (navigateFunc) => {
    // 로그아웃 전에 현재 상태의 userData를 저장
    if (user && user.id) {
      localStorage.setItem(`userData_${user.id}`, JSON.stringify(user.films || {}));
    }
    localStorage.removeItem('currentUser');
    setUser(null);
    if (navigateFunc) {
      navigateFunc("/login");
    }
  };
  
  // 사용자 필름 데이터 (시청중, 찜한 영상, 본 영상)를 업데이트하는 함수
  const updateUserFilms = (category, movieId, action) => {
    if (!user || !user.films || !user.films[category]) { // user.films[category]가 있는지 확인
      console.warn("로그인되지 않았거나 유효하지 않은 카테고리입니다.");
      return;
    }

    // user 상태를 불변적으로 업데이트합니다.
    setUser(prevUser => {
      if (!prevUser) return null; // 유저가 없으면 업데이트 안함

      const newFilms = { ...prevUser.films }; // films 객체 복사
      let updatedList = [...(newFilms[category] || [])]; // 해당 카테고리 리스트 복사

      if (action === 'add' && !updatedList.includes(movieId)) {
        updatedList.push(movieId);
      } else if (action === 'remove') {
        updatedList = updatedList.filter(id => String(id) !== String(movieId));
      }
      
      newFilms[category] = updatedList; // 업데이트된 리스트를 새 films 객체에 할당

      const updatedUser = { ...prevUser, films: newFilms }; // 전체 user 객체 업데이트

      // 로컬 스토리지 'currentUser' 업데이트 (films 포함)
      localStorage.setItem('currentUser', JSON.stringify(updatedUser));
          
      // -- 중요: registeredUsers에서도 해당 사용자의 userData를 업데이트 --
      const registeredUsers = JSON.parse(localStorage.getItem('registeredUsers') || '[]');
      // 각 사용자를 확인하고 userData가 없으면 추가
      registeredUsers.forEach(user => {
        if (!user.userData) {
          user.userData = { '시청중': [], '찜한 영상': [], '본 영상': [] };
        }
      });      
      const userIndex = registeredUsers.findIndex(u => u.userId === updatedUser.id);

      if (userIndex !== -1) {
        registeredUsers[userIndex].userData = newFilms; // films를 userData에 할당
        localStorage.setItem('registeredUsers', JSON.stringify(registeredUsers));
        console.log('registeredUsers userData 업데이트 완료:', registeredUsers[userIndex].userData);
      } else {
        console.warn('registeredUsers에서 사용자를 찾을 수 없습니다:', updatedUser.id);
      }
      // -------------------------------------------------------------

      return updatedUser; // 새로운 user 객체 반환
    });
  };

  return (
    <UserContext.Provider value={{ user, signup, login, logout, setUser, updateUserFilms  }}> {/* setUser도 Context에 포함 */}
      {children}
    </UserContext.Provider>
  );
};