import { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../contexts/UserContext';

export const Login = () => {
  const [userId, setUserId] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  // UserContext에서 login 함수 가져오기
  const { login } = useContext(UserContext);

  const handleLogin = (e) => {
    e.preventDefault();
    setError('');

    if (!userId || !password) {
      setError('아이디와 비밀번호를 입력해주세요.');
      return;
    }

    // UserContext의 login 함수를 호출하여 로그인 처리
    const userInfo  = login({ userId, password }); // 인자로 userId, password 전달
    if (userInfo ) {
      if (userInfo.role === 'admin') {
        navigate('/admin/dashboard');
      } else {
        navigate('/');
      }
  }
  };

  return (
    <>
      <div className="title">
        <h3>로그인</h3>
      </div>  
      <section className="at1 sectionClass">
        <form onSubmit={handleLogin} className="login">
          <table className="login">
            <tbody>
              <tr className="login">
                <td className="login">
                  <input 
                    type="text" 
                    placeholder="ID를 입력하세요"
                    value={userId}
                    onChange={(e) => setUserId(e.target.value)}
                  />
                </td>
              </tr>
              <tr className="login">
                <td className="login">
                  <input 
                    type="password" 
                    placeholder="비밀번호를 입력하세요"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </td>
              </tr>
            </tbody>
          </table>
          {error && <p className="error-message">{error}</p>}
          <div className="button_group">
            <button id="login_id" type="submit">로그인</button>
            <button id="search_id" type="button">아이디찾기</button>
            <button id="search_pswd" type="button">비밀번호찾기</button>
          </div>
        </form>
        
        <div className="simple_box">
            <div className="simple_login">
                <p>간편 로그인</p>
                <hr></hr>
                <div>
                  <a href="#카카오"><img src={process.env.PUBLIC_URL + "/img/kakao_message_social_logo_icon.png"} alt="카카오"/></a>
                  <a href="#네이버"><img src={process.env.PUBLIC_URL + "/img/naver_icon.png"} alt="네이버"/></a>
                  <a href="#구글"><img src={process.env.PUBLIC_URL + "/img/google_brand_branding_logo_network_icon.png"} alt="구글"/></a>
                  <a href="#페이스북"><img src={process.env.PUBLIC_URL + "/img/facebook_logo_media_popular_icon.png"} alt="페북"/></a>
                  <a href="#라인"><img src={process.env.PUBLIC_URL + "/img/line_media_social_icon.png"} alt="라인"/></a>
                </div>
            </div>

        </div>
      </section>
    </>
  );
};