import { useState } from 'react';
import { useNavigate } from 'react-router-dom'; // navigate를 위해 import

export const Subscribe = () => {
  const navigate = useNavigate(); // useNavigate 훅 사용
  
  // 상태 선언
  const [name, setName] = useState('');
  const [userId, setUserId] = useState('');
  const [password, setPassword] = useState('');
  const [passwordConfirm, setPasswordConfirm] = useState('');
  const [phone, setPhone] = useState('');
  const [birth, setBirth] = useState('');
  const [address, setAddress] = useState('');
  const [detailAddress, setDetailAddress] = useState('');
  const [emailId, setEmailId] = useState('');
  const [emailDomain, setEmailDomain] = useState('');
  const [error, setError] = useState('');
  const [isEmailDomainDirect, setIsEmailDomainDirect] = useState(false); // 이메일 도메인 직접 입력 상태

  // 이메일 도메인 선택 변경 핸들러
  const handleEmailDomainChange = (e) => {
    const selectedDomain = e.target.value;
    setEmailDomain(selectedDomain);
    setIsEmailDomainDirect(selectedDomain === '직접입력');
  };

  // 회원가입 제출 처리
  const handleSubmit = async (e) => {
    e.preventDefault();

    // 기본 유효성 검사 (모든 필수 정보 입력 여부)
    if (!name || !userId || !password || !passwordConfirm || !phone || !birth || !address || !emailId || !emailDomain) {
      setError('모든 필수 정보를 입력해주세요.');
      return;
    }
    // 비밀번호 일치 확인
    if (password !== passwordConfirm) {
      setError('비밀번호가 일치하지 않습니다.');
      return;
    }
    // 이메일 직접 입력 선택 시 도메인 직접 입력 여부 확인
    if (isEmailDomainDirect && !emailDomain) {
        setError('이메일 도메인을 직접 입력해주세요.');
        return;
    }

    try {
      // ==========================================================
      // [중요] 여기에 실제 백엔드 API 호출 로직을 넣을 수 있음.
      // 예시: fetch('/api/signup', { ... });
      // 현재는 서버 연동 없이 임시로 성공 처리.
      // ==========================================================

      // 임시 성공 처리 (실제 API 연동 전까지)
      const newUser = {
          name,
          userId,
          password, // 실제 환경에서는 비밀번호를 바로 저장하지 않고 해시 처리.
          phone,
          birth,
          address: address + (detailAddress ? ` ${detailAddress}` : ''),
          email: `${emailId}@${emailDomain}`,
          role: 'user', // 기본적으로 일반 사용자
          userData: {
            '시청중': [],
            '찜한 영상': [],
            '본 영상': []
          }          
      };
      
      // 로컬 스토리지에 사용자 정보 저장
      // 실제 환경에서는 DB에 저장하고, 로그인 시에만 JWT 토큰 등을 localStorage에 저장.
      // 현재는 회원가입 즉시 임시적으로 localStorage에 저장함
      // (관리자 페이지에서 user 데이터가 변경될 수 있으니, 이 로직은 추후 DB 연동 시 변경될 수 있음.)
      const existingUsers = JSON.parse(localStorage.getItem('registeredUsers')) || [];
      // ID 중복 확인
      if(newUser.userId === 'admin'){
        setError('admin은 사용할 수 없는 아이디 입니다.');
        return;
      }
      if (existingUsers.some(u => u.userId === newUser.userId)) {
        setError('이미 사용 중인 아이디입니다. 다른 아이디를 사용해주세요.');
        return;
      }
      localStorage.setItem('registeredUsers', JSON.stringify([...existingUsers, newUser]));

      alert('회원가입이 완료되었습니다!');
      setError(''); // 오류 메시지 초기화
      navigate('/login'); // 회원가입 성공 후 로그인 페이지로 이동
      
    } catch (error) {
      console.error('회원가입 처리 중 오류:', error);
      setError('서버 통신 중 오류가 발생했습니다.');
    }
  };

  return(
    <>
      <div className="title">
          <h3>회원가입</h3>
      </div>
      <section className="sectionClass">
        <form onSubmit={handleSubmit} className="subs_formClass">
          <div className="s1">
            <table className="join">
                <colgroup>
                  <col className="c1"/><col className="c2"/><col className="c3"/><col className="c4"/><col className="c5"/>
                </colgroup>
                <tbody>
                  <tr>
                    <td className="col1">이름</td>
                    <td colSpan={3}><input type="text" placeholder="이름을 입력하세요" value={name} onChange={(e)=>setName(e.target.value)} required /></td>
                    <td></td>
                  </tr>
                  <tr>
                    <td className="col1">아이디</td>
                    <td colSpan={3}><input type="text" placeholder="아이디를 입력하세요" value={userId} onChange={(e)=>setUserId(e.target.value)} required /></td>
                    <td></td>
                  </tr>
                  <tr>
                    <td className="col1">비밀번호</td>
                    <td colSpan={3}><input type="password" placeholder="비밀번호를 입력하세요" value={password} onChange={(e)=>setPassword(e.target.value)} required /></td>
                    <td></td>
                  </tr>
                  <tr>
                    <td className="col1">비밀번호확인</td>
                    <td colSpan={3}><input type="password" placeholder="비밀번호를 다시 입력하세요" value={passwordConfirm} onChange={(e)=>setPasswordConfirm(e.target.value)} required /></td>
                    <td></td>
                  </tr>
                  <tr><td className="col1" style={{height:"10px"}}></td><td></td><td></td><td></td><td></td></tr>
                  <tr>
                    <td className="col1">전화번호</td>
                    <td colSpan={3}><input type="tel" placeholder="010-1234-5678" value={phone} onChange={(e)=>setPhone(e.target.value)} required /></td>
                    <td></td>
                  </tr>
                  <tr>
                    <td className="col1">생년월일</td>
                    <td colSpan={3}><input type="date" value={birth} onChange={(e)=>setBirth(e.target.value)} required /></td>
                    <td></td>
                  </tr>
                  <tr>
                    <td className="col1">주소</td>
                    <td colSpan={3}><input type="text" placeholder="주소를 입력하세요" value={address} onChange={(e)=>setAddress(e.target.value)} required /></td>
                    <td></td>
                  </tr>
                  <tr>
                    <td className="col1">상세주소</td>
                    <td colSpan={3}><input type="text" placeholder="상세주소를 입력하세요" value={detailAddress} onChange={(e)=>setDetailAddress(e.target.value)} /></td>
                    <td></td>
                  </tr>
                  <tr>
                    <td className="col1">이메일</td>
                    <td><input type="text" value={emailId} onChange={(e)=>setEmailId(e.target.value)} required /></td>
                    <td>@</td>
                    <td>
                      <input 
                        type="text" 
                        value={isEmailDomainDirect ? emailDomain : ''} 
                        onChange={(e)=>setEmailDomain(e.target.value)}
                        disabled={!isEmailDomainDirect} // '직접입력' 선택 시에만 활성화
                        placeholder={isEmailDomainDirect ? "도메인 직접 입력" : ""}
                        required={isEmailDomainDirect}
                      />
                    </td>
                    <td>
                        <select className="subscribe_user" value={emailDomain} onChange={handleEmailDomainChange}>
                          <option value="" disabled hidden>email</option>
                          <option value="직접입력">직접입력</option>
                          <option value="naver.com">naver.com</option>
                          <option value="gmail.com">gmail.com</option>
                          <option value="daum.net">daum.net</option>
                          <option value="hanmail.net">hanmail.net</option>
                          <option value="hotmail.com">hotmail.com</option>
                        </select>
                    </td>
                  </tr>
                </tbody>
            </table>
          </div>
          {error && <p style={{color: 'red', textAlign: 'center', marginTop: '10px'}}>{error}</p>}
          <br></br>
          <div className="s2">
            <input className="final_confirm" type="submit" value="가입" />
          </div>       
        </form>
      </section>
    </>
  );
}