import { useState, useEffect } from 'react';
import '../../styles/UserManagement.css'; // 필요한 경우 스타일 파일 추가

export const UserManagement = () => {
  // 사용자 목록 상태
  const [users, setUsers] = useState([]);
  // 선택된 사용자 ID (삭제나 수정 시 사용)
  const [selectedUserId, setSelectedUserId] = useState(null);
  // 검색어 상태
  const [searchTerm, setSearchTerm] = useState('');
  // 정렬 상태
  const [sortField, setSortField] = useState('name');
  const [sortDirection, setSortDirection] = useState('asc');

  // 컴포넌트 마운트 시 로컬 스토리지에서 사용자 정보 불러오기
  useEffect(() => {
    loadUsers();
  }, []);

  // 로컬 스토리지에서 사용자 정보 불러오기
  const loadUsers = () => {
    const storedUsers = JSON.parse(localStorage.getItem('registeredUsers')) || [];
    setUsers(storedUsers);
  };

  // 사용자 삭제 핸들러
  const handleDeleteUser = (userId) => {
    if (window.confirm('정말 이 사용자를 삭제하시겠습니까?')) {
      const updatedUsers = users.filter(user => user.userId !== userId);
      localStorage.setItem('registeredUsers', JSON.stringify(updatedUsers));
      setUsers(updatedUsers);
      
      // 현재 로그인된 사용자가 삭제되는 경우 로그아웃 처리
      const currentUser = JSON.parse(localStorage.getItem('currentUser'));
      if (currentUser && currentUser.id === userId) {
        localStorage.removeItem('currentUser');
        // 실제 애플리케이션에서는 로그아웃 로직 추가 필요
      }
    }
  };

  // 검색 필터링
  const filteredUsers = users.filter(user => 
    user.name?.toLowerCase().includes(searchTerm.toLowerCase()) || 
    user.userId?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.email?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // 정렬 함수
  const sortedUsers = [...filteredUsers].sort((a, b) => {
    let fieldA = a[sortField] || '';
    let fieldB = b[sortField] || '';
    
    // 문자열 비교
    if (typeof fieldA === 'string' && typeof fieldB === 'string') {
      fieldA = fieldA.toLowerCase();
      fieldB = fieldB.toLowerCase();
    }
    
    if (fieldA < fieldB) return sortDirection === 'asc' ? -1 : 1;
    if (fieldA > fieldB) return sortDirection === 'asc' ? 1 : -1;
    return 0;
  });

  // 정렬 방향 토글 함수
  const toggleSort = (field) => {
    if (sortField === field) {
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortDirection('asc');
    }
  };

  return (
    <div className="user-management">
      <h2>사용자 관리</h2>
      
      <div className="search-bar">
        <input 
          type="text" 
          placeholder="이름, 아이디 또는 이메일로 검색" 
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </div>
      
      <div className="user-list">
        <table>
          <thead>
            <tr>
              <th onClick={() => toggleSort('name')}>
                이름 {sortField === 'name' && (sortDirection === 'asc' ? '↑' : '↓')}
              </th>
              <th onClick={() => toggleSort('userId')}>
                아이디 {sortField === 'userId' && (sortDirection === 'asc' ? '↑' : '↓')}
              </th>
              <th onClick={() => toggleSort('email')}>
                이메일 {sortField === 'email' && (sortDirection === 'asc' ? '↑' : '↓')}
              </th>
              <th onClick={() => toggleSort('phone')}>
                전화번호 {sortField === 'phone' && (sortDirection === 'asc' ? '↑' : '↓')}
              </th>
              <th onClick={() => toggleSort('birth')}>
                생년월일 {sortField === 'birth' && (sortDirection === 'asc' ? '↑' : '↓')}
              </th>
              <th>관리</th>
            </tr>
          </thead>
          <tbody>
            {sortedUsers.length > 0 ? (
              sortedUsers.map((user) => (
                <tr key={user.userId}>
                  <td>{user.name}</td>
                  <td>{user.userId}</td>
                  <td>{user.email}</td>
                  <td>{user.phone}</td>
                  <td>{user.birth}</td>
                  <td>
                    <button 
                      className="delete-btn"
                      onClick={() => handleDeleteUser(user.userId)}
                    >
                      삭제
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="6" className="no-users">등록된 사용자가 없습니다.</td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};