import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, A11y } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

import { initialCategories } from '../data/CategoryArray';

import { Link } from 'react-router-dom';
import { useContext } from 'react';
import { MovieContext } from '../contexts/MovieContext';
import { UserContext } from '../contexts/UserContext';  

export const FilmsSection = ({title, names}) =>{

  const { movies: allMovies } = useContext(MovieContext);
  const { user, updateUserFilms } = useContext(UserContext);

  const getFilmsByIds = (idList) => {
    if (!Array.isArray(idList)) return []; 
    return idList
      .map(id => allMovies.find(m => m.id == id))
      .filter(Boolean); // ID에 맞는 영화가 없으면 제거
  };

  const handlePlus = (e, movieId) => {
    e.preventDefault(); 
    e.stopPropagation();
    console.log("현재 user 상태:", user);
    if (user && updateUserFilms) {
      updateUserFilms('찜한 영상', movieId, 'add'); // '찜한 영상' 카테고리에 영화 추가
      // alert('영상이 찜한 영상에 추가되었습니다!');
    } else { // user가 null이면 로그인 요청
        alert('로그인 후 이용해 주세요.');
    }
  };

  const handlePlay = (e, movieId) => {
    e.preventDefault(); 
    e.stopPropagation();
    console.log("현재 user 상태:", user);
    if (user && updateUserFilms) {
      updateUserFilms('시청중', movieId, 'add');  // '시청중' 카테고리에 영화 추가 
      updateUserFilms('본 영상', movieId, 'add'); // '본 영상' 카테고리에 영화 추가 
      // alert('영상이 찜한 영상에 추가되었습니다!');
    } else { // user가 null이면 로그인 요청
        alert('로그인 후 이용해 주세요.');
    }    
  }

  const handleMinus = (e, movieId, categoryName) => {
    e.preventDefault(); 
    e.stopPropagation();
    console.log("삭제 시도:", categoryName, movieId);
    console.log("현재 user.films[categoryName]:", user?.films?.[categoryName]);
    // 사용자 카테고리만 제거 대상이므로, 해당 카테고리에 한정하여 호출.
    // '시청중', '찜한 영상', '본 영상' 중 하나라면 작동.
    if (user && updateUserFilms && ['시청중', '찜한 영상', '본 영상'].includes(categoryName)) {
      updateUserFilms(categoryName, String(movieId), 'remove'); // 해당 카테고리에서 영화 제거
      // alert('영상이 컬렉션에서 제거되었습니다!');
      console.log("삭제 후 user.films[categoryName]:", user?.films?.[categoryName]);
    }
    console.log("movieId 타입:", typeof movieId);
    console.log("user.films[categoryName] 타입:", user?.films?.[categoryName].map(i => typeof i));
  };
  
  return (

    <div className="sectionClass">
      <div className="title">
        <br></br><h3>{title}</h3>
      </div>

      {names.map((categoryName) => {
        let filmsToDisplay = [];
        let isUserCategory = false; // 해당 카테고리가 사용자 데이터 기반인지 여부

        // 카테고리 이름에 따라 데이터 소스 결정
        if (['시청중', '찜한 영상', '본 영상'].includes(categoryName)) {
          // 사용자 관련 카테고리
          isUserCategory = true;
          // user가 로그인되어 있고 user.films에 해당 카테고리가 있으면 해당 데이터를 사용
          // 없으면 (로그인 안했거나 데이터 없음) 빈 배열 사용
          filmsToDisplay = getFilmsByIds(user?.films?.[categoryName] || []); 
        } else {
          // 일반 카테고리 (인기작, 신작, 추천 영상)
          const initialCat = initialCategories.find(cat => cat.name === categoryName);
          if (initialCat && initialCat.films) {
            // initialCategories의 films는 Set 형태일 수 있으므로 배열로 변환
            filmsToDisplay = getFilmsByIds(Array.from(initialCat.films));
          } else {
             // 혹시 initialCategories에 해당 카테고리가 없거나 films 필드가 비어있다면, 대체 로직
             if (categoryName === "인기작") {
                 filmsToDisplay = allMovies.slice(0, 10);
             } else if (categoryName === "신작") {
                 filmsToDisplay = allMovies.slice(10, 20);
             } else if (categoryName === "추천 영상") {
                 filmsToDisplay = allMovies.slice(20, 30);
             }
          }
        }

        // 보여줄 영화가 없는 경우 처리: 제목과 함께 "내용 없음" 메시지 표시
        if (filmsToDisplay.length === 0) {
            return (
              <div key={categoryName} className="category-block">
                <h4>{categoryName}</h4>
                {categoryName==='시청중' ?
                  <p>아직 {categoryName}인 영상이 없습니다.</p>
                :
                  <p>아직 {categoryName}이 없습니다.</p>
                }
              </div>
            );
        }

        // 영화가 있다면 Swiper와 함께 렌더링
        return (
          <div key={categoryName} className="category-block">
            <h4>{categoryName}</h4>
            <Swiper
              key={user?.id + categoryName + (user?.films?.[categoryName]?.length || 0)}
              modules={[Navigation, Pagination, A11y]}
              spaceBetween={10}
              slidesPerView={6}
              navigation
              pagination={{ clickable: true }}
            >
              {filmsToDisplay.map((movie) => (
                <SwiperSlide key={movie.id}>
                  <Link to={`/movie/${movie.id}`}> 
                    <div className="movie-card">
                      <img className="thumb" src={process.env.PUBLIC_URL + movie.src} alt={movie.name} />
                      <div className="icons">
                        <button className="icon1" onClick={(e) => {handlePlay(e, movie.id)}}>
                        <img src={process.env.PUBLIC_URL + "/img/play_icon.png"} alt="play"/></button>
                        
                        {/* Plus 버튼: 'Main Page'일 때만 표시 */}
                        {(title === 'Main Page') && (
                          <button className="icon2" onClick={(e) => {handlePlus(e, movie.id)}}>
                            <img src={process.env.PUBLIC_URL + "/img/plus_icon.png"} alt="plus"/>
                          </button>
                        )}
                        
                        {/* Minus 버튼: '나의 컬렉션'일 때만 표시 (isUserCategory 여부는 이제 카테고리 이름으로 판단) */}
                        {(title === '나의 컬렉션') && isUserCategory && (
                          <button className="icon2" onClick={(e) => {
                                                    e.preventDefault();
                                                    e.stopPropagation();
                                                    handleMinus(e, movie.id, categoryName);
                                                    console.log("마이너스 버튼 클릭됨:", movie.id, categoryName);
                                                    }}
                          >
                            <img src={process.env.PUBLIC_URL + "/img/minus_icon.png"} alt="minus"/>
                          </button>
                        )}                      
                      </div>
                      <p>{movie.name}</p>
                    </div>
                  </Link>
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        );
      })}
    </div>
  );
};