const MOVIE_STORAGE_KEY = 'my_movie_data';

/**
 * 로컬 스토리지에서 영화 데이터를 가져옵니다.
 * @returns {Array|null} 저장된 영화 데이터 배열 또는 null
 */
export const getMoviesFromLocalStorage = () => {
  try {
    const serializedMovies = localStorage.getItem(MOVIE_STORAGE_KEY);
    if (serializedMovies === null) {
      return null;
    }
    return JSON.parse(serializedMovies);
  } catch (error) {
    console.error("로컬 스토리지에서 영화 데이터를 불러오는 중 오류 발생:", error);
    return null;
  }
};

/**
 * 영화 데이터를 로컬 스토리지에 저장합니다.
 * @param {Array} movies - 저장할 영화 데이터 배열
 */
export const setMoviesToLocalStorage = (movies) => {
  try {
    const serializedMovies = JSON.stringify(movies);
    localStorage.setItem(MOVIE_STORAGE_KEY, serializedMovies);
  } catch (error) {
    console.error("로컬 스토리지에 영화 데이터를 저장하는 중 오류 발생:", error);
  }
};