import { useState, useEffect, useContext } from "react";
import { Link, useNavigationType, useLocation } from "react-router-dom";
import { MODES } from "../data/CategoryArray";
import { MovieContext } from '../contexts/MovieContext';
import { UIContext } from "../contexts/UIContext";

export const Search = () => {

  const { setMode } = useContext(UIContext); // UIContext에서 setMode 가져오기
  const { movies } = useContext(MovieContext);
  const navigationType = useNavigationType();
  const [key, setKey] = useState('');
  const [result, setResult] = useState([]);
  const [selectedGenre, setSelectedGenre] = useState('전체');
  const location = useLocation();

  useEffect(() => {
    // 컴포넌트가 마운트될 때만 실행되도록 의존성 배열을 비워둡니다
    const savedResult = sessionStorage.getItem('searchResult');
    if (savedResult) {
      setResult(JSON.parse(savedResult));
    }
  }, []); // 빈 배열은 컴포넌트 마운트 시에만 실행

  // URL 검색 파라미터 처리 추가
  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const searchQuery = queryParams.get('q');
    
    // URL에 검색어가 있으면 검색어 설정 및 검색 실행
    if (searchQuery) {
      setKey(searchQuery);
      executeSearch(searchQuery, selectedGenre);
    } else {
      // 검색어가 없고 새로 접근한 경우(POP이 아닌 경우) 결과 초기화
      if (navigationType !== "POP") { // POP은 뒤로가기/앞으로가기
        setResult([]);
        sessionStorage.removeItem('searchResult');
      } else {
        // 뒤로가기로 왔을 때만 세션 스토리지에서 데이터 복원
        const savedResult = sessionStorage.getItem('searchResult');
        if (savedResult) {
          setResult(JSON.parse(savedResult));
        }
      }
    }
  }, [location.search, navigationType, selectedGenre, movies]);  

  // 검색 실행 함수 (재사용성을 위해 분리)
  const executeSearch = (searchText, genre) => {
    const filteredMovies = movies.filter((movie) => {
      // 장르 필터링 ('전체'인 경우 모든 장르 포함)
      const genreMatch = genre === '전체' || movie.info.broadcasting === genre;
      
      // 키워드 검색 (여러 필드에서 검색)
      const keywordMatch = 
        movie.name.toLowerCase().includes(searchText.toLowerCase()) || 
        movie.info.casting.some(actor => actor.includes(searchText)) ||
        movie.info.broadcasting.toLowerCase().includes(searchText.toLowerCase()) ||
        // movie.info.summary && movie.info.summary.toLowerCase().includes(searchText) ||
        movie.info.productionYear.includes(searchText);
      
      // 두 조건 모두 만족해야 함
      return genreMatch && keywordMatch;
    });
    
    setResult(filteredMovies);
    sessionStorage.setItem('searchResult', JSON.stringify(filteredMovies));
  };

  // 검색 버튼 클릭 핸들러
  const handleSubmit = (e) => {
    e.preventDefault();
    executeSearch(key, selectedGenre); // 이미 만들어둔 executeSearch 함수 활용
  };

  // 컴포넌트 마운트 시 결과 복원
  // useEffect(() => {
  //   // 뒤로가기로 왔을 때만 세션 스토리지에서 데이터 복원
  //   if (navigationType === "POP" || !result.length) {
  //     const savedResult = sessionStorage.getItem('searchResult');
  //     if (savedResult) {
  //       setResult(JSON.parse(savedResult));
  //     }
  //   }
  // }, [navigationType, result.length]);

  return(
    <>
      <div className="title">
          <h3>상세검색</h3>
      </div>
      <section className="sectionClass">
          <form onSubmit={handleSubmit}>
            <div className="s1">
              <table>
                  <tbody>
                    <tr><td className="col1">카테고리</td><td>
                      <select 
                        className="search_genre" 
                        defaultValue=""
                        onChange={(e)=> setSelectedGenre(e.target.value)}
                        >
                          <option value="" disabled hidden>장르선택</option>
                          <option>전체</option>
                          <option>한국영화</option>
                          <option>한국드라마</option>
                          <option>해외영화</option>
                          <option>해외드라마</option>
                          <option>예능</option>
                          <option>스포츠</option>
                          <option>다큐멘터리</option>
                          <option>키즈</option>
                      </select>
                    </td><td></td></tr>
                    <tr><td className="col1">키워드</td>
                      <td><input type="text" placeholder="키워드" value={key}
                          onChange={(e)=>(setKey(e.target.value))}
                          /></td><td></td>
                    </tr>
                  </tbody>
              </table>
            </div>
          </form>
          <br></br>
          <div className="s2">
            <table>
                <tbody>
                  <tr><td className="col1"></td><td><input 
                          className="final_confirm" type="submit" value="검색"
                          onClick={handleSubmit}
                          /></td><td></td></tr>
                </tbody>
            </table> 
          </div>      
      </section>
      <section className="search_result" style={{ display: result.length > 0 ? 'block' : 'none' }}>
        <h3>검색결과</h3>
        <table className="result_table">
          <tbody>
            {result.map((m) => (
              <tr key={m.id}>
                <td className="column1">
                  <Link to={`/movie/${m.id}`}>
                    <img className="thumb" src={process.env.PUBLIC_URL + m.src} alt={m.name} />
                  </Link>
                  <div className="icons">
                    <button className="icon"><img src={process.env.PUBLIC_URL + "/img/play_icon.png"} alt="play" /></button>
                    <button className="icon"><img src={process.env.PUBLIC_URL + "/img/plus_icon.png"} alt="plus" /></button>
                  </div>
                  <p style={{ fontSize: '1rem' }}>{m.name}</p>
                </td>
                <td className="column2">{m.info?.summary || '요약 정보 없음'}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </section>
    </>
  );
}